div.tablePlated+table {
  border-spacing: 1px;
  border-collapse: separate; }

div.tablePlated+table td, div.tablePlated+table th {
  background-color: lightblue; }

div.tableGridded+table {
  border-spacing: 0;
  border-collapse: collapse; }

div.tableGridded+table td, div.tableGridded+table th {
    border: solid 1px dodgerblue; }

div.container {
  width: 100%;
  hheight: 100%;
  bbackground: -webkit-linear-gradient(top, #088fad 20%, #00d5ff 100%);

  div.titulo {
    ffont-size: 5vw;
    background-color: blue;
    text-align: center;
    ppadding: 1vw;
    position: relative; }

  div.col-left {
    width: 20%;
    display: inline-block;
    bbackground-color: red;
    ffont-size: 3vw;
    text-align: center;
    ppadding: 1vw 0vw 1vw 0vw; }

  div.col-right {
    width: 80%;
    bbackground-color: green;
    display: inline-block;
    margin-right: 0vw;
    ffont-size: 3vw;
    text-align: center;
    ppadding: 1vw 0vw 1vw 0vw;
    min-width: 4vw;
    position: relative;
    ffloat: right; }

  div.col-centro {
    min-width: 20%;
    display: inline-block;
    bbackground-color: red;
    ffont-size: 3vw;
    text-align: left;
    padding: 0vw 0vw 0vw 2vw; } }

